import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';
import {
    MatButtonModule,
    MatCheckboxModule,
    MatNativeDateModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSidenavModule,
    MatSlideToggleModule,
    MatSortModule,
    MatTabsModule,
    MatTableModule,
    MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';
import { SharedModule } from '../../shared/shared.module';

import { LocationsService } from './locations.service';

import { FuseLocationsComponent } from './locations/locations.component';
import { FuseLocationsListComponent } from './locations-list/locations-list.component';
import { FuseLocationFormComponent } from './location-form/location-form.component';

const routes: Routes = [
    {
        path: '**',
        component: FuseLocationsComponent,
        resolve  : {
            location: LocationsService
        }
    }
];

@NgModule({
    declarations   : [
        FuseLocationsComponent,
        FuseLocationsListComponent,
        FuseLocationFormComponent,
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        MatButtonModule,
        MatCheckboxModule,
        MatNativeDateModule,
        MatDatepickerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSidenavModule,
        MatSlideToggleModule,
        MatSortModule,
        MatTabsModule,
        MatTableModule,
        MatToolbarModule,

        FuseSharedModule,
        FuseConfirmDialogModule,
        SharedModule
    ],
    providers      : [
        LocationsService
    ],
    entryComponents: [
        FuseLocationFormComponent
    ]
})
export class FuseLocationsModule
{
}



